.App {
  background-color: #000;
  color: white;
  display: flex;
  flex: 1;
  flex-direction: column;
}

.Container {
  /* min-height: 100vh; */
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Info {
  text-align: center;
}

.FooterLink {
  color: #50E3C2;
}

.Code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  background-color: #111;
  border-radius: 4px;
  padding: 2px 4px;
  font-size: xx-large;
}
